@use "global" as *;

.completion__text {
  color: $white;
  font-size: rem(20);
  letter-spacing: 0.1em;
  text-align: center;
  margin-bottom: rem(80);
  @include mq(md) {
    font-size: rem(30);
  }
}

.completion__text br {
  @include mq(md) {
    display: none;
  }
}

.completion__link {
  text-align: center;
}